<template>
	<view class="list u-margin-24 u-p-24">
		<view class="top u-flex u-row-between u-p-b-24 u-border-bottom">
			<view class="name u-font-xl u-type-error u-font-bold u-skeleton-rect">热销产品</view>
			<view class="btn u-skeleton-rect" @click="click">
				<text class="u-m-r-8 u-font-sm u-tips-color">查看全部</text>
				<u-icon class="u-tips-color" name="more-circle" />
			</view>
		</view>
		<view class="wrap">
			<view class="item u-flex u-p-t-24 u-p-b-24 u-border-bottom" v-for="(item,index) in list" :key="item.id">
				<view class="pic u-m-r-24 u-skeleton-rect">
					<u-image width="150rpx" height="150rpx" border-radius="6rpx" :src="item.pictures+'?imageMogr2/crop/216x148'"></u-image>
				</view>
				<view class="info">
					<view class="tit u-skeleton-rect u-font-lg u-line-1 u-font-bold u-m-b-8">{{item.name}}</view>
					<view class="tag u-skeleton-rect u-m-b-8">
						<u-tag class="u-m-r-12" v-for="(oItem,oIndex) in item.tags" :key="oItem.id" size="mini" :text="oItem.name" />
					</view>
					<view class="price u-skeleton-rect u-font-xl u-type-warning u-font-bold">{{item.inPrice.toFixed(2)}} <text class="txt">元/起</text></view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: []
			}
		},
		methods: {
			click() {
				uni.switchTab({
					url:'/pages/product/index',
					success(res) {
						console.log(res)
					},
					fail(err) {
						console.log(err)
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.list {
		background-color: #fff;
		border-radius: 12rpx;

		.wrap {
			.item {
				&:last-of-type {
					padding-bottom: 0 !important;

					&::after {
						display: none;
					}
				}

				.info {
					overflow: hidden;
				}
			}
		}
	}
</style>
